body {
  height: 100vh;
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab) 0 0 / 400% 400%;
  animation: gradient 15s infinite;
}

@keyframes gradient {
  0% {
    background-position: 0%;
  }

  50% {
    background-position: 100%;
  }

  100% {
    background-position: 0%;
  }
}

h1 {
  text-align: center;
  background-color: aqua solid 3px;
}

h2 {
  flex-basis: 50%;
}

img {
  background-color: aqua solid 3px;
  flex-basis: 10%;
}

.flex {
  flex-direction: row;
  justify-content: center;
  display: flex;
}

.boxen {
  width: fit-content;
  background: #73ad2126;
  border-radius: 25px;
  align-self: center;
  padding: 1rem;
}

.buttonDiv {
  align-self: center;
}

button {
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  margin: 4px 2px;
  padding: 16px 32px;
  font-size: 16px;
  text-decoration: none;
  transition-duration: .4s;
  display: inline-block;
}

button:hover {
  color: #fff;
  background-color: #214022;
}

